.header {
  display: none;
}

.ttl-page {
  font-size: 23px;
  font-weight: bold;
  margin-bottom: 40px;
}
@media only screen and (max-width: 767px) {
  .ttl-page {
    font-size: 21px;
    margin-bottom: 30px;
  }
}

.sec-tokushoho {
  padding: 80px 0 100px;
}
.sec-tokushoho .inner {
  max-width: 950px;
  margin: 0 auto;
}
.sec-tokushoho .inner .bx {
  margin-bottom: 40px;
}
.sec-tokushoho .inner .bx:last-child {
  margin-bottom: 0;
}
.sec-tokushoho .inner .bx .ttl {
  font-size: 16px;
  letter-spacing: 0.1em;
  line-height: 1.4em;
  padding: 15px;
  background: #efefef;
  margin-bottom: 15px;
}
.sec-tokushoho .inner .bx .txt {
  font-size: 14px;
  letter-spacing: 0.1em;
  line-height: 1.8em;
}
.sec-tokushoho .inner .bx .txt a {
  color: #000;
  text-decoration: underline;
}
.sec-tokushoho .inner .bx .img {
  width: 100%;
  max-width: 600px;
  margin: 20px 0;
  display: block;
}
.sec-tokushoho .inner .bx table {
  margin-bottom: 20px;
}
.sec-tokushoho .inner .bx table th {
  font-size: 14px;
  vertical-align: top;
}
.sec-tokushoho .inner .bx table td {
  font-size: 14px;
  vertical-align: top;
}
.sec-tokushoho .inner .bx table .tar {
  text-align: right;
}
.sec-tokushoho .inner .bx.table01 table {
  width: 100%;
}
.sec-tokushoho .inner .bx.table01 table th {
  padding-left: 0;
  padding-right: 0;
}
.sec-tokushoho .inner .bx.table01 table td {
  padding-left: 5px;
  padding-right: 5px;
}
.sec-tokushoho .inner .bx.table02 table {
  width: 50%;
}
@media only screen and (max-width: 767px) {
  .sec-tokushoho {
    padding: 80px 0 70px;
  }
  .sec-tokushoho .inner .bx {
    margin-bottom: 30px;
  }
  .sec-tokushoho .inner .bx .ttl {
    font-size: 14px;
  }
  .sec-tokushoho .inner .bx .txt {
    font-size: 12px;
  }
  .sec-tokushoho .inner .bx.table02 table {
    width: 100%;
  }
  .sec-tokushoho .inner .bx.table02 table th:first-child,
.sec-tokushoho .inner .bx.table02 table td:first-child {
    width: 60%;
  }
}

.sec-terms-of-service {
  padding: 80px 0 100px;
}
.sec-terms-of-service .inner {
  max-width: 950px;
  margin: 0 auto;
  font-weight: normal;
}
.sec-terms-of-service .inner p {
  font-size: 16px;
  line-height: 1.6em;
  letter-spacing: 0.05em;
  margin: 20px 0;
}
.sec-terms-of-service .inner p.tr {
  text-align: right;
  margin-top: 15px;
}
.sec-terms-of-service .inner ol {
  margin: 20px 0 0 35px;
}
.sec-terms-of-service .inner ol li {
  font-size: 16px;
  line-height: 1.6em;
  letter-spacing: 0.05em;
  list-style: outside decimal;
  margin-top: 10px;
}
.sec-terms-of-service .inner ol li ol {
  margin-bottom: 20px;
}
.sec-terms-of-service .inner h2 {
  font-size: 21px;
  font-weight: bold;
  margin: 40px 0 15px;
}
@media only screen and (max-width: 767px) {
  .sec-terms-of-service {
    padding: 80px 0 70px;
  }
  .sec-terms-of-service .block {
    max-width: 100%;
  }
  .sec-terms-of-service .block p {
    font-size: 14px;
    margin: 15px 0;
  }
  .sec-terms-of-service .block p.tr {
    margin-top: 15px;
  }
  .sec-terms-of-service .block ol {
    margin: 15px 0 0 25px;
  }
  .sec-terms-of-service .block ol li {
    font-size: 14px;
    margin-top: 7px;
  }
  .sec-terms-of-service .block ol li ol {
    margin-bottom: 15px li;
    margin-bottom-letter-spacing: 0;
  }
  .sec-terms-of-service .block h2 {
    font-size: 18px;
    margin: 30px 0 10px;
  }
}

.sec-privacy {
  padding: 80px 0 100px;
}
.sec-privacy .inner {
  max-width: 950px;
  font-weight: normal;
  margin: 0 auto;
}
.sec-privacy .inner p {
  font-size: 16px;
  margin-bottom: 20px;
  line-height: 1.6em;
  letter-spacing: 0.05em;
}
.sec-privacy .inner p:last-child {
  margin-bottom: 0;
}
@media only screen and (max-width: 767px) {
  .sec-privacy {
    padding: 80px 0 70px;
  }
  .sec-privacy .block {
    max-width: 100%;
  }
  .sec-privacy .block p {
    font-size: 14px;
    margin-bottom: 15px;
  }
}